import React from "react";
import {PATH} from "./Routes";
import { NavLink } from "react-router-dom";

function Header() {
    return (
        <div>
            <NavLink to={PATH.PRE_JUNIOR} activeClassName="selected">
                PreJunior
            </NavLink>
            <NavLink to={PATH.JUNIOR} activeClassName="selected">
                Junior
            </NavLink>
            <NavLink to={PATH.JUNIOR_PLUS} activeClassName="selected">
                Junior Plus
            </NavLink>

        </div>
    );
}

export default Header;
